@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,400;0,700;1,400&family=Montserrat:wght@700&display=swap);
body{margin:0;padding:0;font-family:sans-serif}

:root{font-size:16px}h1,h2,h3,p{margin:0;padding:0}body{padding:0;margin:0}.wrapper{height:100vh;width:100%;display:flex;flex-direction:column;align-items:center;background-color:#282828}.wrapper .form-wrapper{display:flex;flex-direction:column;margin-top:8%;max-width:1000px}.wrapper .form-wrapper .form-title{font-size:4vw;width:100%;font-family:"Montserrat", sans-serif;color:#fff;padding-bottom:2rem}.wrapper .form-wrapper .horizontalLine{background-color:#d02035;width:35%;height:0.7rem;margin-bottom:2rem}.wrapper .form-wrapper .thankYou-msg{font-family:"Montserrat", sans-serif;font-weight:bold;font-size:2.1875rem;padding-bottom:2rem;color:#fff}.wrapper .form-wrapper .form-description{font-family:"Merriweather", serif;color:#fff;font-size:1.25rem;padding-bottom:2rem}.wrapper .form-wrapper .form-inputs{width:100%;display:grid;grid-template-columns:1fr 1fr;grid-gap:0.5rem;padding-bottom:2rem}.wrapper .form-wrapper .form-inputs input{padding:0}.wrapper .form-wrapper .form-inputs input,.wrapper .form-wrapper .form-inputs .dropdown-form{font-family:"Merriweather", serif;font-style:italic;color:#797979;font-style:1rem;padding-left:0.5rem;height:2.8rem;border-radius:none;border:unset}.wrapper .form-wrapper .form-inputs .form-email{display:flex;flex-direction:column}.wrapper .form-wrapper .form-signup-btn{padding-top:1rem}.wrapper .form-wrapper .form-signup-btn .form-btn,.wrapper .form-wrapper .form-signup-btn a{font-family:"Merriweather", serif;font-weight:bold;color:#fff;width:100%;background-color:#d02035;font-size:1.25rem;text-decoration:none;padding:8px 0px;border:none;border-radius:3px}input.error{border:2px solid #d02035}.errorMessage{font-family:"Merriweather", serif;color:#fff;font-size:0.75rem;display:relative;margin-left:29%}.dropdown{position:relative;display:inline-block;vertical-align:middle}.dropdown::before,.dropdown::after{content:"";position:absolute;pointer-events:none}.dropdown::after{content:"\25BC";font-size:0.725em;line-height:1;right:1.2em;top:50%;margin-top:-0.5em}.dropdown .dropdown-form{width:100%}.dropdown select{color:#fff;font-size:inherit;border:0;margin:0;text-indent:0.01px;-webkit-appearance:button}@media only screen and (max-width: 1000px){.wrapper .form-wrapper{padding:2rem}.wrapper .form-wrapper .form-inputs{grid-template-columns:100%}.errorMessage{float:right}}

